:root {
  --aside-size: 23rem;
  --cover-font-s: 16px;
  --cover-font-c: #fefefe;
  --cover-s: 300px;
  --cover-bg: #de4f45;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: #2b2b2b;
}

.wrapper {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template: 100% / var(--aside-size) 1fr;
  column-gap: 10px;
}

.aside {
  overflow-y: auto;
  background-color: #fefefe;
  box-shadow: 0 0 1px 0 rgba(21, 21, 21, 0.3);

  &__form {
    display: flex;
    flex-flow: column;
    justify-items: center;
    padding: 10px;

    &__select {
      margin-top: 25px !important;
    }
  }

  &__label {
    font-family: 'Roboto';
    font-weight: 200;
    font-size: 18px;
    text-transform: capitalize;
    margin: 20px 0 10px 5px;
  }
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;

  .cover {
    display: flex;
    justify-content: space-between;
    width: var(--cover-s);
    height: var(--cover-s);
    background-color: var(--cover-bg);
    box-shadow: 0 0 10px 0px rgba(21, 21, 21, 0.6);
    transition: background-color 0.1s 0s linear;
    padding: 30px;
    position: relative;

    &__tunes {
      position: absolute;
      margin-left: 15px;
      font-size: var(--cover-font-s);
      font-family: 'Roboto';
      color: var(--cover-font-c);
      font-weight: 200;
      line-height: 1.85;
      text-shadow: 1px 1px rgba(21, 21, 21, 0.3);
    }

    &__art {
      margin: var(--cover-art-pos);
      width: var(--cover-art-s);
      height: var(--cover-art-s);
      box-shadow: 0 0 3px 0px rgba(21, 21, 21, 0.6);
    }
  }
}
